import React from 'react';
import style from "./AboutProduct.module.css"

const AboutProduct = ({toggleProductWindow}) => {
    return (
        <>
        <div className={`${style.window} wow zoomIn`}>
            <div className={`${style.shadow} wow zoomIn`}></div>
            
        
        <div className={style.infoArea}>
        <div className={`${style.close} wow zoomIn`} onClick={toggleProductWindow} ></div>
        </div>
        
        </div>

        </>
    );
};

export default AboutProduct;